<template>
  <div class="loginpage">
    <v-card class="logincard pb-5 px-4 ma-2" elevation="12" max-width="500px">
      <v-card-title>
        <div class="text-center py-8 ma-auto">
          <h2>Mentorship</h2>
        </div>
      </v-card-title>
      <v-card-text class="mt-n6">
        <v-form>
          <v-text-field
            hint="e.g. example@exmple.com"
            placeholder="e.g. example@exmple.com"
            label="E-mail"
            v-model.trim="cred"
            outlined
            dense
          />
          <v-text-field
            hint="e.g. 601ca5x1d4780"
            placeholder="e.g. 601ca5x1d4780"
            :append-icon="show ? 'mdi-eye' : 'mdi-eye-off'"
            autocomplete="off"
            name="password"
            label="Password"
            :type="show ? 'text' : 'password'"
            @click:append="show = !show"
            @keyup.enter="login"
            required
            outlined
            dense
            v-model.trim="password"
          />
        </v-form>
        <v-sheet
          color="error"
          class="pa-3"
          dark
          v-if="this.$store.state.error"
          >{{ this.$store.state.error }}</v-sheet
        >
      </v-card-text>
      <v-card-actions class="mt-n3">
        <v-btn
          color="blue cursive"
          dark
          class="ma-auto py-2"
          large
          block
          :loading="this.$store.state.loading"
          @click="login"
          >Login</v-btn
        >
      </v-card-actions>
      <v-card-actions>
        <v-btn
          block
          class="indigo"
          dark
          href="https://docs.google.com/forms/d/e/1FAIpQLSfFbRYnKVjHXcwqnxAZJyT8ra4ENfEJAtfifyjWa6AcVmVY3g/viewform"
          target="_blank"
          large
        >
          Register
        </v-btn>
      </v-card-actions>
    </v-card>
  </div>
</template>

<script>
export default {
  data() {
    return {
      cred: "",
      password: "",
      show: false,
    };
  },
  methods: {
    login() {
      if (
        /^(([^<>()[\].,;:\s@"]+(\.[^<>()[\].,;:\s@"]+)*)|(".+"))@(([^<>()[\].,;:\s@"]+\.)+[^<>()[\].,;:\s@"]{2,})$/i.test(
          this.cred
        ) &&
        this.password
      ) {
        this.$store.commit("SET_ERROR", "");

        let userdata = {
          cred: this.cred,
          password: this.password,
        };
        this.$store.dispatch("userLogin", userdata);
      } else {
        this.$store.commit("SET_ERROR", "Please provide valid credential.");
      }
    },
  },
};
</script>

<style lang="scss" scoped>
.loginpage {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #fff;
  min-height: 100vh;
}

.logincard {
  border-radius: 20px !important;
  min-width: 300px;
  /* margin-top: 10px; */
  background: #ffffff;
  /* box-shadow: -18px 18px 36px 5px #e7e0e0 !important; */
}

@media screen and (min-width: 768px) {
  .loginpage {
    margin: 0 auto;
  }
  .logincard {
    min-width: 300px;
    margin: 0 auto;
    min-width: 400px;
  }
}

.crashcourselogo {
  height: 100%;
  width: 100%;
  object-fit: contain;
}
.crashcourselogo__container {
  height: 200px;
  overflow: hidden;
}
</style>